.loader-wrapper {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.loader {
  width: 300px;
  height: 300px;
  display: inline-block;
  position: relative;
}
.loader::after,
.loader::before {
  content: "";
  box-sizing: border-box;
  width: 300px;
  height: 300px;
  border-radius: 50%;
  border: 2px solid #2c2c2c;
  position: absolute;
  left: 0;
  top: 0;
  animation: animloader 3s linear infinite;
  opacity: 0;
}
.loader::after {
  animation-delay: 1.5s;
}

@keyframes animloader {
  0% {
    transform: scale(0);
    opacity: 1;
  }
  100% {
    transform: scale(1);
    opacity: 0;
  }
}
